.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination.active {
  display: flex;
}

.tui-pagination {
  padding: 40px 0 40px 0;
  display: none;
  align-items: center;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    width: 450px;
    padding: 50px 0 50px 0;
    margin-left: auto;
    margin-right: auto;
  }
}

.tui-page-btn,
.tui-page-btn:hover,
.tui-page-btn:visited,
.tui-page-btn:active {
  text-decoration: none;
}

.tui-page-btn {
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #ffffff;
  background: #ff6b08;
  cursor: default;
}

.tui-page-btn.tui-first,
.tui-page-btn.tui-last {
  background-color: #f7f7f7;
  border-radius: 5px;

  @media screen and (max-width: 768px) {
    display: none;
  }
}

.tui-is-selected {
  background-color: #ff6b08;
  border-radius: 5px;
  color: #ffffff;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-ico-ellip {
  height: 24px;
  width: 24px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-next {
  background-color: #f7f7f7;
}
.tui-prev {
  background-color: #f7f7f7;
}

.tui-page-btn.tui-next-is-ellip,
.tui-page-btn.tui-prev-is-ellip {
  background-color: #f7f7f7;

  @media screen and (max-width: 768px) {
    display: none;
  }
}
